.wrap {
  width: 1100px;
  margin: 0 auto;
}
.wrapss {
  width: 1100px;
  margin: 0 auto;
}
.wraps {
  width: 1280px;
  margin: 0 auto;
}
.wrapper {
  position: relative;
  display: block;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  padding: 230px 0;
  background-color: #f9f9f9;
}
.navbar {
  font-weight: 900;
  color: #fff !important;
  box-shadow: 0 0px 0px 0 rgba(0, 0, 0, 0.16), 0 0px 0px 0 rgba(0, 0, 0, 0.12) !important;
}
.hanger {
  position: absolute !important;
  right: 0 !important;
}
.collaper {
  position: relative !important;
  top: -3em !important;
}
.navbar.navbar-dark .navbar-nav .nav-item .nav-link {
  color: #fff !important;
  font-family: "Google Sans" !important;
  font-weight: 400 !important;
  font-size: 16px !important;
  transition: 0.35s;
}
.navbar-expand-md .navbar-nav .nav-link {
  padding-right: 2rem !important;
  padding-left: 2rem !important;
}
.nav-link {
  font-size: 16px !important;
}
.bg-primary {
  transition: 0.3s all ease-in-out;
  background-color: #fff !important;
}
.navbar.scrolling-navbar.top-nav-collapse {
  padding-bottom: -10em !important;
  margin-bottom: -10em !important;
}
span.text h1 {
  font-size: 46px;
  color: #fff;
  font-family: "Google Sans Bold";
  text-transform: capitalize;

  line-height: 56px;
  margin-bottom: 20px;
}
span.text p {
  line-height: 28px;
  font-size: 17px;
  color: #fff;
  max-width: 445px;
  margin-bottom: 35px;
  text-align: justify;
}
.casestudies span.text p {
  line-height: 28px;
  font-size: 17px;
  color: #fff;
  min-width: 1000px;
  max-width: 700px !important;
  margin-bottom: 35px;
  text-align: justify;
}
.teams span.text p {
  line-height: 28px;
  font-size: 17px;
  color: #fff;
  min-width: 1000px;
  max-width: 700px !important;
  margin-bottom: 35px;
  text-align: justify;
}
.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}
.zoomIn {
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn;
}
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.welcome {
  position: relative;
  top: -4em;
}
img.wbackground {
  position: relative;
  max-width: 149%;
  max-height: 149%;
  margin: 0;
  padding: 0;
}
img {
  vertical-align: middle;
  border-style: none;
}
.mobile_pic {
  position: absolute;
  overflow: hidden;
  top: 20px;
  right: 0;
  max-width: 770px;
  z-index: 2;
}
.ridicule {
  position: relative !important;
  top: -8em !important;
  display: inline-block !important;
}
nav.bigger ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  padding-left: 2em;
}
a.link {
  color: #fff !important;
}
a.link:hover {
  border-bottom: 2px solid #e86850;
}

nav.bigger ul li {
  --c: #587498;
  color: #fff;
  font-size: 16px;
  background-color: #e86850;
  border: 0.2em solid #e86850;
  border-radius: 0.3em;
  width: 100%;
  padding: 0em 1em 0em 1em;
  height: 2.8em;
  font-family: "Google Sans";
  letter-spacing: 0.1em;
  text-align: center;
  line-height: 2.2em;
  position: relative;
  overflow: hidden;
  z-index: 1;
  cursor: pointer;
  transition: 0.5s;
}

nav.bigger ul li span {
  position: absolute;
  cursor: pointer;
  width: 70%;
  height: 100%;
  background-color: var(--c);
  border: 0.2em solid var(--c);
  transform: translateY(150%);
  border-radius: 50%;
  left: calc((var(--n) - 1) * 25%);
  transition: 0.5s;
  transition-delay: calc((var(--n) - 1) * 0.1s);
  z-index: -1;
}

nav.bigger ul li:hover {
  color: #fff;
  border: 0.2em solid var(--c);
}

nav.bigger ul li:hover span {
  transform: translateY(0) scale(2);
}

nav.bigger ul li span:nth-child(1) {
  --n: 1;
}

nav.bigger ul li span:nth-child(2) {
  --n: 2;
}

nav.bigger ul li span:nth-child(3) {
  --n: 3;
}

nav.bigger ul li span:nth-child(4) {
  --n: 4;
}
nav.smaller ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav.smaller ul li {
  --c: #e86850;
  color: darkblue;
  font-size: 16px;
  background-color: #fff;
  border: 0.2em solid #fff;
  border-radius: 0.3em;
  width: 32%;
  padding: 0em 0em 0em 0em;
  height: 2.8em;
  font-family: "Google Sans";
  letter-spacing: 0.1em;
  text-align: center;
  line-height: 2.2em;
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: 0.5s;
  cursor: pointer;
}
nav.playb ul li {
  --c: #e86850;
  color: #fff;
  font-size: 16px;
  background-color: #5d64fb !important;
  border: 0.2em solid #5d64fb;
  border-radius: 0.3em;
  width: 37%;
  padding: 0em 0em 0em 0em;
  height: 2.8em;
  font-family: "Google Sans";
  letter-spacing: 0.1em;
  text-align: center;
  line-height: 2.2em;
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: 0.5s;
  cursor: pointer;
}

nav.smaller ul li span {
  position: absolute;
  width: 25%;
  height: 100%;
